<template>
	<view>
		<uni-forms ref="form" :model="formData" :rules="formRules" labelPosition="top">
			<uni-group>
				<uni-forms-item label="申请者:">
					<uni-easyinput v-model="formData.order" disabled></uni-easyinput>
				</uni-forms-item>
			</uni-group>

			<uni-group>
				<uni-forms-item label="使用者:">
					<uni-data-checkbox v-model="eaterSelect" :localdata="eaters" @change="eaterChange">
					</uni-data-checkbox>
				</uni-forms-item>

				<view :style=colleagueStyle>
					<uni-forms-item label="同事账号:">
						<uni-easyinput type="number" v-model="formData.user" placeholder="请输入手机号"></uni-easyinput>
					</uni-forms-item>
				</view>

				<view :style=visitorStyle>
					<uni-forms-item label="公司:">
						<uni-easyinput v-model="formData.company" placeholder="请输入公司名"></uni-easyinput>
					</uni-forms-item>
					
					<uni-forms-item label="人数:">
						<uni-number-box min="2" v-model="formData.num"></uni-number-box>
					</uni-forms-item>

					<uni-forms-item label="接待原因:">
						<uni-easyinput type="textarea" v-model="formData.reason" placeholder="请输入接待原因"></uni-easyinput>
					</uni-forms-item>
					
					<uni-forms-item label="接待日期:">
						<uni-datetime-picker type="date" v-model="formData.comingdate"></uni-datetime-picker>
					</uni-forms-item>
				</view>
			</uni-group>
			
			<uni-group>
				<uni-forms-item label="申请数量:">
					<uni-number-box min="1" v-model="formData.ordernum"></uni-number-box>
				</uni-forms-item>
			</uni-group>

			<button @click="submit">提交</button>
		</uni-forms>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				formData: {
					order: "",
					user: "",
					ordernum: "1",
					company: "",
					num: "2",
					reason: "",
					comingdate: Date.now(),
				},
				eaters: [{
					text: "自己",
					value: "myself"
				}, {
					text: "同事",
					value: "colleague"
				}, {
					text: "访客",
					value: "visitor"
				}],
				eaterSelect: "myself",
				colleagueStyle: "display: none;",
				visitorStyle: "display: none;",
			}
		},
		methods: {
			eaterChange: function(event) {
				if (this.eaterSelect == "myself") {
					this.colleagueStyle = "display: none;";
					this.visitorStyle = "display: none;";
				} else if (this.eaterSelect == "colleague") {
					this.colleagueStyle = "";
					this.visitorStyle = "display: none;";
				} else {
					this.colleagueStyle = "display: none;";
					this.visitorStyle = "";
				}
			},
			submit: function(event) {
				console.log(this.formData);
			}
		}
	}
</script>

<style>
	text {
		margin-left: 20rpx;
	}

	button {
		width: 600rpx;
		height: 100rpx;
		background-color: #f0f8ff;
		font-size: 120%;
		margin-top: 20rpx;
		margin-bottom: 20rpx;
	}
</style>
